@import url('../fonts/stylesheet.css');
@import url('./reset.scss');

$break-laptop: 1390px;
$break-tablet: 1190px;
$break-smart:800px;
$break-phone:640px;

$bgcolor: #131313;
$textcolor: #ffffff;
$textcolor2: #e3b873;
$textcolor3: #363636;
$textcolor4: #181818;


@mixin flex($hor, $vert, $flow) {
  display: flex;
  justify-content: $hor;
  align-items: $vert;
  flex-flow: $flow;
}

@mixin placeholder($color){
  ::-webkit-input-placeholder {
    color: $color;
  }
  
  :-ms-input-placeholder {
    color: $color;
  }
  
  ::placeholder {
    color: $color;
  }
}

body {
  background-color: #fff; //$bgcolor
}

.wrapper {
  @include flex(center, none,column);
  max-width:1440px;
  margin: 0 auto;
  background: #fff;
  overflow:hidden;

  @media screen and (max-width: $break-phone){
    min-width:375px;
    max-width:640px;
  }

}

header, footer {
  @include flex(space-between, center, row);
  width: 89%;
  padding: 24px 83px 22px;
  background: #131313;

  .logo{
    display:flex;
    width:20%;
  }
  
  nav {
    @include flex(flex-start, center, row);
    width:49%;
  }
  
  nav ul {
    @include flex(space-around, center, row);
    list-style: none;
    width:100%;
    margin: 0 0 0 -18px;
  }
  
  nav a {
    color: $textcolor;
    font-family: "FuturaBookC";
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;
  
    &:hover {
      color:$textcolor2;
    }
  }
  
  div.phone {
    @include flex(space-around, center, row);
    width: 29%;
    font-family: "FuturaBookC";
  }
  
  .phone__number{
    color: $textcolor2;
    font-family: "FuturaDemiC";
    font-size: 14px;
    font-weight: 400;
    text-decoration: underline;
  
    &:hover{
      text-decoration: none;
      color: $textcolor;
    }
  }
  
  .phone__request{
    color:$textcolor;
    font-family: "FuturaDemiC";
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 400;
    background: $textcolor2;
    text-decoration: none;
    padding: 12px 35px;
    border: 1px solid $textcolor2;
    border-radius: 2px;
    
    &:hover{
      border: 1px solid $textcolor;
      background: none;
    }
  }

  @media screen and (max-width: $break-tablet) {
    width: 100%;
    padding: 23px 0 22px;
    justify-content: space-evenly;

    .logo{
      width: 24%;
    }

    nav{
      width:44%;

      ul{
        margin:0;
      }

      a{
        font-size: 12px;
      }
    }

    div.phone{
      width:22%;

      a:first-child{
        display:none
      }

      a:last-child{
        min-width:100px;
      }
    }
  }

  @media screen and (max-width: $break-smart){



    nav{
      display:none;
    }

    div.phone{
      width:22%;

      a{
        font-size: 14px;
        color: $textcolor;
        text-decoration: none;
      }

      a:first-child{
        display:flex
      }

      a:last-child{
        display: none;
      }
    }

  }

  @media screen and (max-width: $break-phone){
    justify-content: space-around;
    padding: 20px 0 21px;

    .logo{
      width: 52%;
      img{
        width: 149px;
      }
    }

    div.phone{
      width: 28%;

      .phone__number{
        font-size: 12px;
      }

    }
  }
}

footer{
  width: 90%;
  padding: 50px 83px 46px;
  background: #131313;

  @media screen and (max-width: $break-tablet) {
    width: 94%;
    padding: 23px 27px 22px;

    .logo{
      width: 25%;
    }

    nav{
      width:45%;

      a{
        font-size: 12px;
      }
    }

    div.phone{
      width:22%;

      a:first-child{
        display:none
      }

      a:last-child{
        min-width:100px;
      }
    }
  }

  @media screen and (max-width: $break-phone){
    display:none;
  }
}

.main{
  @include flex(flex-start, flex-start, row);
  background-color: $bgcolor;
  background-image: url('../images/background-main.jpg');
  background-position: 82px 0%;
  background-repeat: no-repeat;
  padding: 26px 0;

  .main__leftside{
    @include flex(space-around, baseline, column);
    min-height: 863px;
    margin: 37px -110px -200px 0;
  }
  
  .main__social {
    @include flex(center, center, row);
    position: relative;
    transform: rotate(90deg);
    margin: 37px 0px 0 -17px;
  }
  
  .social__instagram{
    color: $textcolor2;
    font-family: "FuturaDemiC";
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    margin: -36px 0 0 0;
  
    &:hover{
      color: $textcolor;
    }
  
    &::after{
      content: '';
      display: flex;
      height: 2px;
      width: 51px;
      background: $textcolor2;
      position: relative;
      top: -7px;
      left: 92px;
    }
  
    &:hover::after{
      background: $textcolor;
    }
  }
  
  .main__scroll {
    background: $bgcolor;
    padding: 33px 49px 39px 49px;
    margin: 44px 0 0 0;
  
    a{
      color: $textcolor;
      font-family: "YesevaOne";
      font-size: 14px;
      font-weight: 400;
      line-height: 19.69px;
      text-transform: uppercase;
  
      &:hover {
        color: $textcolor2;
      }
    }
  
    span{
      width:29px;
      height:29px;
      display: inline-block;
      background-size: cover;
      background: url('../images/icons/scroll-down.svg');
      margin: 0 0 -8px 18px;
    }
  
  }
  
  .main__content{
    margin: 135px 0 0 0px;
  }
  
  .main__content > h1 {
    color: $textcolor;
    font-family: "YesevaOne";
    font-size: 40px;
    font-weight: 400;
    line-height: 53.91px;
    text-transform: uppercase;
    margin: 28px 4px 16px;
    letter-spacing: 0px;
    
    span {
      color: $textcolor2
    }
  
  }
  
  .main__content > p {
    font-family: "FuturaBookC";
    color: $textcolor;
    font-size: 22px;
    font-weight: 400;
    line-height: 30.94px;
    margin: 0 0 0 5px;
    max-width: 420px;
  }
  
  .main__cta {
    @include flex(flex-start, center, row);
    font-family: "FuturaDemiC";
    color:$textcolor;
    font-size: 13px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    margin: 45px 0 0 -6px;
    width: 90%;
    border-radius: 2px;
  }
  
  .main__calc{
    background: $textcolor2;
    border: 1px solid $textcolor2;
    padding: 18px 41px;
    border-radius: 2px;
    margin: 0 11px 0 11px;
    
    &:hover{
      background: none;
      border: 1px solid $textcolor;
    }
  }
  
  .main__submit{
    border: 1px solid $textcolor;
    padding: 18px 24px;
    border-radius: 2px;
    margin: 0 0px 0 7px;
  
    &:hover{
      background: $textcolor2;
      border: 1px solid $textcolor2;
    }
  }

  @media screen and (max-width: $break-tablet) {
    background-position: 67% 0;

    .main__leftside{
      display: none;
    }
  
    .main__content{
      @include flex(center, center, column);
      width:100%;
      margin:0;
  
      h1{
        font-size: 33px;
        line-height: 40px;
        margin: 12px 4px 16px;
      }
  
      p{
        font-size: 18px;
        line-height: 27px;
        margin: 0 0 0 5px;
      }
  
      div.main__cta{
        width: auto;
        margin: 28px 0 17px -6px;
      }
  
    }
  
  }

  @media screen and (max-width: $break-smart){
    background-image: url(../images/background-main-alt.jpg); 
    background-size: cover;
    background-position: 51% 0px;

    .main__content > h1{
      font-size: 23px;
      line-height: 31px;
      margin: 57px 0 12px -44px;
    }

    .main__content > p{
      line-height: 24px;
      margin: 0 0 0 -30px;
      max-width: 309px;
    }

    .main__content{
      div.main__cta{
        max-width: 338px;
        margin: 34px 0 17px -6px;
        flex-flow: column;
        width:65%;
  
        .main__calc{
          padding: 18px 46px;
          margin: 0 11px 0 15px;
          width: 100%;
        }
    
        a.main__submit{
          padding: 18px 46px;
          margin: 26px 11px 18px 15px;
          width: 100%;
  
          span{
            display:none;
          }
        }
      }
    }

  }
}

// completed projects
.completed-projects{
  @include flex(center,center ,column);
  background: $bgcolor;
  padding: 60px 0;

  h2{
    color: $textcolor2;
    font-family: "YesevaOne";
    font-size: 28px;
    font-weight: 400;
    letter-spacing: -2.89px;
    line-height: 37.97px;
    text-transform: uppercase;

    &:after{
      content: "";
      display: inline-block;
      width: 92px;
      height: 2px;
      background: $textcolor2;
      position: relative;
      top: -9px;
      left: 42px;
    }
  }

  p{
    color: $textcolor;
    font-family: "FuturaBookC";
    font-weight: 400;
    margin: 22px 0 0 0;
    font-size: 18px;
    line-height: 29.53px;
    max-width: 414px;
  }

  div.cp__nav{
    @include flex(flex-end,center ,row);
    width: 74%;
  
    ul{
      @include flex(space-around, baseline , row);
      width:65%;
  
    }
  
    a{
      color: rgba(255, 255, 255, 0.3);
      font-family: "YesevaOne";
      font-size: 14px;
      font-weight: 400;
      line-height: 19.69px;
      text-transform: uppercase;
      padding: 0 0 7px 0;
  
      &:hover{
        color: $textcolor2;
        border-bottom: 2px solid $textcolor2;
      }
    }
  
    a.active{
      color: $textcolor2;
      border-bottom: 2px solid $textcolor2;
    }
  
  
  }
  
  div.cp__project{
    @include flex(flex-start, center ,row);
    width: 100%;
  
    div.cp__description{
      width: 40%;
      margin: 48px 0 0 102px;
    }

    div.cp__project__slider{
      display:none;
    }
  
    div.cp__slider{
      width: 59%;
  
      .cp_slider_slides{
        @include flex(flex-start, center ,row);
        margin: 34px 0 0 4px;
        width:1400px;
  
        img{
          margin: 0 21px 0 0;
          width:679px;
        }
      }
    }
  }
  
  .cp__slider__nav{
    @include flex(space-between, center ,row);
    width: 89%;
    margin: 0 auto -39px auto;
    position: relative;
    top: 257px;
    z-index: 100;
  
    a{
      @include flex(center, center ,row);
      box-shadow: 0 0 15px rgba(227, 184, 115, 0.5);
      background-color: $textcolor2;
      width:54px;
      height:54px;
      border-radius: 50%;
      font-size: 33px;
      font-weight: bold;
      border: 1px solid $textcolor2;
  
      &:hover{
        background:transparent;
        border: 1px solid $textcolor;
      }
  
      &:first-child{
        visibility: hidden;
      
      }
    }
  
  }
  
  ul.cp__data {
    @include flex(flex-start, center, row);
    flex-wrap: wrap;
    margin: 46px 0 0 0;
  
    li{
      width: 39%;
      height: 40px;
      margin: 0 0 67px 0;
    }
    
    h4 {
      color: $textcolor2;
      font-family: "YesevaOne";
      font-size: 18px;
      font-weight: 400;
      line-height: 25.31px;
      text-transform: uppercase;
    }
  
    p {
      color: $textcolor;
      font-family: "FuturaBookC";
      font-size: 15px;
      font-weight: 400;
      line-height: 21.09px;
      margin: 3px 0 0 0;
    }
  }
  
  .cp__arrownav{
    @include flex(space-between, center, row);
    width: 40%;
    margin: 31px 0 0 6px;
  
    li{
  
      a{
        display: flex;
        width: 37px;
        height: 2px;
        background: $textcolor;
  
        &:hover{
          opacity: 0.3;
        }
      }
  
      &:first-child > a::before{
        content: "\27F5";
        font-size: 24px;
        position: relative;
        top: -12px;
        left: -5px;
      
        &:hover{
          opacity:0.3;
        }
      }
  
      &:last-child > a::before{
        content:'\27F6';
        font-size: 24px;
        position: relative;
        top: -12px;
        left: 14px;
      }
      
      &:not(:first-child):not(:last-child) > a {
        display: flex;
        width: 10px;
        height:10px;
        border-radius: 50%;
        background:$textcolor;
        opacity:0.3;

        &:hover{
          opacity: 1;
        }
        
      }
      &:not(:first-child):not(:last-child) > a.cp__arrownav__item--active {
        opacity:1;
      }
    }
  
  }

  @media screen and (max-width: $break-tablet) {
    padding:30px;

    div.cp__nav{
      display:none;
    }

    div.cp__project{
      div.cp__description{
        @include flex(center, center, column);
        width:100%;
        margin:0;
  
  
        h2{
          text-align: center;
          
          &:after{
            display: none;
          }
        }

        p{
          line-height:25px
        }

        div.cp__project__slider{
          display: flex;
          justify-content: center;
          align-items: center;
          position: relative;
          width:95%;
          margin: 30px 0 0 0;

          .cp__project__slider__nav{
            position:absolute;
            @include flex(space-between, center, row);
            width: 92%;

            a{
              width: 35px;
              height: 35px;
              background: $textcolor2;
              border-radius: 50%;
              display: flex;
              justify-content: center;
              font-size: 27px;
              line-height: 29px;
              cursor: pointer;
              border: 1px solid $textcolor2;

              &:hover{
                background: none;
                border: 1px solid $textcolor;
              }
            }
          }

          .cp__project__slider__slides > img{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            

            &:first-child{
              display: none;
            }
          }
        }

        ul.cp__data{
          justify-content: center;

          li{
            @include flex(normal, normal, column);
            width:40%;
            margin: 0 0 56px 0;
          }
        }

        ul.cp__arrownav{
          display:none;
        }
      }
    }
    div.cp__slider{
      display:none;
    }

  }

  @media screen and (max-width: $break-smart){
    padding: 6px;

    div.cp__project{
      div.cp__description{
        h2{
          font-size: 22px;
          letter-spacing: -0.9px;
          margin: 28px 0 0 2px;
          line-height: 30px;
        }

        p{
          font-size: 14px;
          line-height: 23px;
          text-align: center;
          max-width: 310px;
          margin: 25px 0 0 0;
        }

        div.cp__project__slider{
          margin: 33px 0 0 0;
        }

        ul.cp__data{
          justify-content: normal;
          align-items: normal;
          margin: 39px 0 0 11px;

          li{
            width: 46%;
            margin: -7px 0 62px 0;
          }

          h4{
            font-size: 14px;
          }

          p{
            text-align: left;
            margin: 4px 0 0 0;
            line-height: 20px;
          }
        }


      }
    }
  }
}

// online control offer block
.online-control-offer{
  @include flex(center, center, column);
  background: linear-gradient(180deg, rgba(19,19,19,1) 35%, rgba(255,255,255,1) 35%);

  h2{
    color: $textcolor;
    font-family: "YesevaOne";
    font-size: 20px;
    font-weight: 400;
    line-height: 28.13px;
    text-transform: uppercase;
        margin: -5px 0 73px 6px;

    &:before{
      content: '';
      background:url('../images/icons/online-controls.svg');
      width: 148px;
      height: 35px;
      background-repeat: no-repeat;
      display: block;
      position: relative;
      top: 76px;
      left: 30px;
    }
  }

  iframe {
    border: none;
    width:817px;
    height:434px;
  }

  p {
    color: $textcolor3;
    font-family: "FuturaBookC";
    font-size: 20px;
    font-weight: 400;
    line-height: 35.16px;
    text-align: center;
    margin: 85px 0 100px 0;
    max-width: 672px;


    &:before{
      content:'';
      display: flex;
      width: 56px;
      height: 2px;
      background-color: #e3b873;
      position: relative;
      top: -23px;
      margin: 0 auto;
    }
  }

  form{
    font-family: "FuturaBookC";
    color: $textcolor3;
    margin: -2px 0 50px 0;
    width: 62%;

    & > span{
      display: table;
      margin: 7px auto 0 auto;
      padding: 0 0 0 30px;
      width: 30%;
    }

    div.online__control__order__inputs{
      @include flex(space-between, baseline, row);
      width: 94%;
    }

    label{
      font-size: 15px;
      font-weight: 400;
      opacity: 0.5;
      position: relative;
      top: -22px;
      left: 56px;

      span:last-child{
        display: none;
      }
    }

    input[type=text], input[type=tel]{
      border:none;
      border-bottom: 2px solid rgba(54, 54, 54, 0.3);
      font-size: 15px;
      font-weight: 400;
      line-height: 33px;
      width:233px;
      padding: 0 2px 0px 0;
      margin: 0 0 0 10px;
      outline: none;
      color: $textcolor3;
    }

    input[type=submit]{
      background: $textcolor2;
      color: $textcolor;
      font-family: "FuturaDemiC";
      font-size: 15px;
      font-weight: 400;
      line-height: 26.37px;
      text-transform: uppercase;
      border: 1px solid $textcolor2;
      width:200px;
      margin: 0 16px;
      padding: 11px 0;
      cursor: pointer;
      outline: none;

      &:hover{
        color: $textcolor2;
        background: none;
        border: 1px solid $textcolor2;
      } 
    }

    div.checkbox{
      display: flex;
      cursor: pointer;
      position: relative;
      width: 33%;
      margin: 27px auto -27px auto;

      input {
        height: 18px;
        width: 18px;
        -webkit-appearance: none;
        -moz-appearance: none;
        -o-appearance: none;
        appearance: none;
        border: 1px solid $textcolor2;
        border-radius: 2px;
        outline: none;
        transition-duration: 0.3s;
        background-color: $textcolor;
        cursor: pointer;
        position: relative;

        &:checked{
          border: 1px solid $textcolor2;
          background-color: $textcolor2;
        }

        &:checked + span::before{
          content: url('data:image/svg+xml;charset=UTF-8, <svg width="10" height="8" viewBox="0 0 10 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M9.03657 0.687256L3.32504 6.39879L0.728882 3.80264" stroke="white" stroke-width="1.5"/></svg>');
          display: block;
          text-align: center;
          position: absolute;
          left: 9px;
          top: 3px;
        }
        

        &:active{
          border: 2px solid #34495E;
        }
      }

    }
    
  }

  @media screen and (max-width: $break-laptop) {
    form > span{
      padding: 0 0 0 100px;
      width: 38%;
    }
  }

  @media screen and (max-width: $break-tablet) {
    iframe{
      width:92%;
    }

    form{
      width:94%;
    }
  }

  @media screen and (max-width: $break-smart){
    background: rgba(19,19,19,1);
    margin: -28px 0 0 0;

    h2{
      font-size: 22px;

      &:before{
        top: 81px;
        left: 39px;
      }
    }

    iframe{
      width: 91%;
      margin: 21px 0 0 0;
      height: 194px;
    }

    p{
      color: $textcolor;
      font-family: "FuturaBookC";
      font-size: 14px;
      line-height: 24px;
      margin: 48px 0 27px 0;
      max-width: 331px;

      &:before{
        top: -14px;
      }
    }

    form{
      width:95%;
      max-width: 378px;
      color:$textcolor;
      @include placeholder($textcolor);
      position: relative;
      font-weight: 300;
      margin: 0 0 -21px 21px;

      div.online__control__order__inputs{
        width: 100%;
        flex-flow:column;

        label{
          top: 4px;
          left: 0px;

          span:first-child{
            display: none;
            
          }
          span:last-child{
            display: flex;

          }
        }

        input[type=text]{
          width: 92%;
          background: none;
          margin: 0 0 58px 0;
          color:$textcolor;
          border-bottom: 1px solid rgba(255, 255, 255, 0.7);
        }

        input[type=submit]{
          width: 95%;
          margin: 0;
          position: relative;
          top: 50px;
        }


      }

      div.checkbox{
        position: relative;
        width: 60%;
        top: -110px;
        left: -77px;
      }

      & > span{
        padding: 0 0 0 0;
        width: 100%;
        position: relative;
        top: -110px;
        left: 24px;
      }

      

    }
    

  }
}

// type of repair
.repair__types{
  @include flex(center, center, column);
  margin: 46px 0 200px 0;

  h2{
    color: $textcolor4;
    font-family:"YesevaOne";
    font-size: 20px;
    font-weight: 600;
    line-height: 28.13px;
    text-transform: uppercase;
    margin: 15px 0 75px 0px;

    &:before{
      content: '';
      background:url('../images/icons/type-of-repair.svg');
      width: 148px;
      height: 35px;
      background-repeat: no-repeat;
      display: block;
      position: relative;
      top: 76px;
      left: 30px;
    }
  }

