// process
.process{
  @include flex(space-around, center, row);
  background-color: $textcolor;
  position: relative;
  margin: 0 0 269px 0;

  &__circle{
    position: absolute;
    left: -162px;
    top: -1px;
    width: 636px;
    height: 633px;
    border-radius: 50%;
    background: 
       linear-gradient($textcolor,$textcolor) padding-box,
       linear-gradient(to right, $textcolor 0%, $textcolor 20%, $textcolor 20%, $textcolor2 100%) border-box;
    border: 3px solid transparent;
  }

  &__steps{
    width: 52%;
    position: relative;
    @include flex(flex-end, normal, row);
    padding: 30px 0 60px 0;
    background-image: url(../images/process-background.png);
    background-repeat: no-repeat;
    background-position:-28px -55px;

    ul{
      font-family: FuturaMediumC;
      font-size: 18px;
      line-height: 21px;
      color: #363636;
      margin: -20px 0 0 0;

      li{
        position: relative;
        &:after{
          content:'';
          position: absolute;
          width:65px;
          height:65px;
          border-radius: 50%;
          background:$textcolor ;
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 90;
        }

        &:before{
          content:'';
          position: absolute;
          width:41px;
          height:41px;
          border-radius: 50%;
          background: $textcolor2;
          display: flex;
          justify-content: center;
          align-items: center;
          z-index: 100;
          font-family: 'YesevaOne';
          color: $textcolor;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 25px;
        }
      }

      li.process__steps__step-1{
        margin: 50px 0 78px 0;

        &:before{
          content:'01';
          top: 3px;
          left: -56px;
        }

        &:after{
          top: -9px;
          left: -68px;
        }
      }

      li.process__steps__step-2{
        margin: 50px 0 141px 75px;

        &:before{
          content:'02';
          top: -6px;
          left: -57px;
        }

        &:after{
          top: -17px;
          left: -69px;
        }
      }

      li.process__steps__step-3{
        margin: 50px 0 130px 98px;

        &:before{
          content:'03';
          top: -8px;
          left: -55px;
        }

        &:after{
          top: -18px;
          left: -67px;
        }
      }

      li.process__steps__step-4{
        margin: 50px 0 50px 33px;

        &:before{
          content:'04';
          top: 2px;
          left: -55px;
        }

        &:after{
          top: -9px;
          left: -67px;
        }
      }
    }
  }

  &__request{
    width: 40%;
    margin: -22px 0 -21px 0;

    form{
      @include flex(space-between, center, column);
      background-color:$bgcolor;
      color:$textcolor;
      width:100%;
      max-width: 506px;
      @include placeholder($textcolor);
      margin: 65px 0 -23px 0;
    }
      
      h3{
        font-family: FuturaDemiC;
        font-size: 20px;
        line-height: 25px;
        text-align: center;
        color:$textcolor;
        margin: 24px 0;
      }

      div{
        width: 100%;
        margin: 32px 0;

        &:before, &:after{
          content: '';
          font-size: 15px;
          font-family: FuturaBookC;
          line-height: 20px;
          color:rgba(255,255,255,0.5);
          position: relative;
        }
      }

      input[type=text]{
        font-family: FuturaBookC;
        font-size: 18px;
        line-height: 25px;
        border:none;
        border-bottom: 1px solid rgba(255,255,255,0.5);
        font-weight: 400;
        padding: 3px 2px 4px 0;
        margin: 0 0 0 10px;
        outline: none;
        background: none;
        max-width:408px;
        width:100%;
        color: $textcolor;
      }

      input[type=submit]{
        background: $textcolor2;
        color: $textcolor;
        font-family: "FuturaDemiC";
        font-size: 13px;
        font-weight: 400;
        line-height: 26.37px;
        text-transform: uppercase;
        border: 1px solid $textcolor2;
        width:212px;
        margin: 4px 49px -4px 49px;
        padding: 11px 0;
        cursor: pointer;
        outline: none;
  
        &:hover{
          color: $textcolor2;
          background: none;
          border: 1px solid $textcolor2;
        } 
      }

      div.process__request__form__input--agree{
        display: flex;
        cursor: pointer;
        position: relative;
        width: 80%;
        margin: -17px 0 0 -11px;
    
        input {
          height: 18px;
          width: 18px;
          -webkit-appearance: none;
          -moz-appearance: none;
          -o-appearance: none;
          appearance: none;
          border: 1px solid $textcolor2;
          border-radius: 2px;
          outline: none;
          transition-duration: 0.3s;
          background-color: $textcolor;
          cursor: pointer;
          position: relative;
    
          &:checked{
            border: 1px solid $textcolor2;
            background-color: $textcolor2;
          }
    
          &:checked + span::before{
            content: url('data:image/svg+xml;charset=UTF-8, <svg width="10" height="8" viewBox="0 0 10 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M9.03657 0.687256L3.32504 6.39879L0.728882 3.80264" stroke="white" stroke-width="1.5"/></svg>');
            display: block;
            text-align: center;
            position: absolute;
            left: 9px;
            top: 3px;
          }
          
    
          &:active{
            border: 2px solid #34495E;
          }
        }
    
      }

      div.process__request__form__input--submit{
        display: flex;
        justify-content: flex-end;
      }

      div.process__request__form__input--name::before{
        content:'Name';
        top: -26px;
        left: 49px;
      }
    
      div.process__request__form__input--phone::before{
        content:'Phone';
        top: -25px;
        left: 49px;
      }
    
      div.process__request__form__input--email::before{
        content:'Email';
        top: -25px;
        left: 50px;
      }
    
      div.process__request__form__input--agree::after{
        content:'I agree to the processing of personal data';
        top: 2px;
        left: 8px;
        letter-spacing: 0.3px;
      }
    }

    p{
      font-family: FuturaDemiC;
      font-size: 20px;
      line-height: 25px;
      color: #E3B873;
      text-shadow: 0px 3px 10px rgba(227, 184, 115, 0.15);
      margin: 0 0 0 129px;

      &:before{
        content: "";
        background-image: url(../images/icons/money-pig.svg);
        width: 72px;
        height: 72px;
        display: flex;
        background-color:$textcolor2;
        background-repeat: no-repeat;
        background-position: 50% 50%;
        border-radius: 50%;
        position: relative;
        top: 62px;
        left: -97px;
          }
    }

    @media screen and (max-width: $break-laptop) {
      margin: 0 0 170px 0;

      .process__circle{
        display:none;
      }

      .process__steps {
        width: 40%;
        background-position: -180px -55px;
    }
    }

    @media screen and (max-width: $break-tablet){
      margin:0 0 92px 0;

      .process__steps{
        display: none;
      }

      .process__request{
        width: 64%;

        p{
          display:none;
        }
      }
      
    }

    @media screen and (max-width: $break-smart){
      display: none;
    }

  }

