        a{
          display: flex;
          width: 37px;
          height: 2px;
          background: $textcolor;
    
          &:hover{
            opacity: 0.3;
          }
        }
    
        &:first-child > a::before{
          content: "\27F5";
          font-size: 24px;
          position: relative;
          top: -12px;
          left: -5px;
        }
    
