          content:'02';
        }

        &:nth-child(3) > a:before{
          content:'03';
        }

        &:nth-child(4) > a:before{
          content:'04';
        }

        &:nth-child(5) > a:before{
          content:'05';
        }

        &:nth-child(6) > a:before{
          content:'06';
        }
      }

      li:last-child{
        padding: 16px 0 0 0;
      }
      
      a{
        font-family: FuturaBookC;
        font-size: 15px;
        line-height: 18px;
        color: $textcolor;
        opacity:0.3;
        position: relative;
        
        &:hover{
          opacity:1;
        }

        &:before{
          content: '';
          font-family: YesevaOne;
          font-style: normal;
          font-weight: normal;
          font-size: 15px;
          line-height: 18px;
          text-transform: uppercase;
          color: #ffffff;
          position: absolute;
          border-bottom: 2px solid #e3b873;
          top: -29px;
          left: 0px;
        }

       
      }
      a.nav__item--active{
        opacity:1;
      }
    }

    &__img{
      margin: 26px 0px -23px 88px;
    }
  }

  @media screen and (max-width: $break-laptop) {
    .steps__info{
      width: 28%;
      margin:0;

      .steps__descr{
        margin: 22px 0 0 0;
      }
    }

    .steps__nav ul{
      display: none;
    }

    .steps__descr__nav{
      margin: 33px 0 0 6px;
    }

    .steps__nav{
      width: 62%;
    }

    .steps__nav__img {
      margin: -12px -30px -23px 25px;
     } 

    .steps__nav__img > img {
      width: 95%;
    }

  }

  @media screen and (max-width: $break-tablet){
    .steps__info {
      h4{
        display: none;
      }

      h2{
        font-size: 24px;
        line-height: 29px;

        &:after{
          display:none;
        }
      }

      p{
        font-size: 15px;
        line-height: 17px;
      }

      ul{
        width:100%;
      }
      
    }
  }

  @media screen and (max-width: $break-smart){
    display: none;
  }

}

// our team block
.team{
  @include flex(center, center, column);
  background: linear-gradient(180deg, rgba(19,19,19,1) 51%, rgba(255,255,255,1) 51%);
  h2{
    color: $textcolor;
    font-family: "YesevaOne";
    font-size: 20px;
    font-weight: 400;
    line-height: 28.13px;
    text-transform: uppercase;
    margin: 2px 0 62px 44px;

    &:before{
      content: '';
      background:url('../images/icons/team.svg');
      width: 148px;
      height: 35px;
      background-repeat: no-repeat;
      display: block;
      position: relative;
      top: 76px;
      left: -4px;
    }
  }

  &__pretext{
    color:$textcolor;
    font-family:"FuturaBookC";
    font-size: 20px;
    line-height: 27px;
    text-align: center;
    max-width: 460px;
  }

  &__wrap{
    @include flex(space-around, center, row);
    margin: 12px 0 0 0;
    width: 79%;
    flex-wrap:wrap;

    &__item{
      min-width:272px;
      

      &:before{
        content: '';
        display: flex;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat; 
        background-color: $textcolor2;
        position: relative;
        top: 35px;
        left: -34px;
        width: 70px;
        height: 70px;
      }

      &:nth-child(1):before{
        background-image: url('../images/icons/team-brain.svg');
        background-position: 50% 50%;
      }
  
      &:nth-child(2):before{
        background-image: url('../images/icons/team-designer.svg');
        background-position: 50% 50%;
      }
  
      &:nth-child(3):before{
        background-image: url('../images/icons/team-suit.svg');
        background-position: 50% 50%;
      }

      &__card{
        background: linear-gradient(180deg, rgba(255,255,255,1) 67%, rgba(227,184,115,1) 67%);
        width: 80%;
        margin: -83px auto 75px auto;
        position: relative;
        padding: 18px 10px;
        box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.05);

        h4{
          font-family: 'YesevaOne';
          font-style: normal;
          font-weight: 600;
          font-size: 18px;
          line-height: 25px;
          text-align: center;
          color:$textcolor3;
        }

        p{
          text-align: center
        }

        p.team__wrap__item__card__status{
          font-family: 'FuturaBookC';
          font-size: 18px;
          line-height: 26px;
          text-align: center;
          color: #959595;
          margin: 4px 0 -4px 0;
        }

        p.team__wrap__item__card__experience{
          color: $textcolor;
          font-family: Yeseva One;
          font-style: normal;
          font-weight: normal;
          font-size: 13px;
          line-height: 20px;
          text-align: center;
          text-transform: uppercase;
          margin: 34px 0 -4px 4px;
        }

      }
    }
      
  }

  &__posttext{
    font-family: 'FuturaBookC';
    font-size: 20px;
    line-height: 34px;
    text-align: center;
    color: $textcolor3;
    margin: -13px 0 0 0;
    max-width: 558px;

    &::before{
      content: "";
      width: 56px;
      height: 1px;
      border-top:1px solid $textcolor2;
      border-bottom:1px solid $textcolor2; 
      display: flex;
      margin: 0 auto;
      position: relative;
      top: -9px;
    }
  }

  @media screen and (max-width: $break-laptop) {
    .team__wrap{
      width: 97%;
    }

    .team__wrap__item:before{
      background-color: transparent;
      top: 70px;
      left: 0px;
      opacity: 0.6;
    }
  }
  @media screen and (max-width: $break-tablet){
    background: $bgcolor;

    .team__pretext{
      line-height: 24px;
      font-size: 18px;
    }

    .team__wrap__item__card{
      margin: -83px auto -10px auto;
    }

    .team__posttext{
      color:$textcolor;
      margin: 75px 0 36px 0;
      line-height: 24px;
      font-size: 18px;
    }

  }

  @media screen and (max-width: $break-tablet){
    margin: -35px 0 0 0;

    h2{
      font-size: 22px;
      margin: 2px 0 62px 27px;
      
      &:before{
        top: 74px;
        left: 4px;
      }
    }

    .team__pretext{
      line-height: 18px;
      font-size: 15px;
      max-width: 323px;
    }

    .team__wrap__item{
      margin: 0px 0 50px 27px;

      &:before{
        background-color: $textcolor2;
        top: 35px;
        left: -35px;
        opacity: 1;
      }
    }

    .team__posttext{
      display: none;
    }
  }


}

