// contacts
.contacts{
  @include flex(center, center, column);
  background-color: $textcolor;
  background-image: url(../images/contacts-background.png);
  background-repeat: no-repeat;
  background-position: 0 205px;

  h2{
    color: $textcolor;
    font-family: "YesevaOne";
    font-size: 20px;
    font-weight: 400;
    line-height: 28px;
    text-transform: uppercase;
    margin: 15px 0 49px 23px;
    position: relative;
  }

  p{
    color:$textcolor2;
    font-family: YesevaOne;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    text-transform: uppercase;
    margin: 50px 0 0 0;
  }
  
  a{
    color:$textcolor2;
    &:hover{
      text-decoration: underline;
      color:$textcolor;
    }
  }

  div.map{
    margin: 0 0 58px 0;
}

  ul{
    @include flex(space-between, center, row);
    width: 5%;
    margin: 23px 0 42px -4px;
    
    a{
      @include flex(center, center, row);
      width:27px;
      height:27px;
      border-radius: 50%;
      background: $textcolor2;
      border:1px solid $textcolor2;
      
      &:hover{
        background:none;
        border:1px solid $textcolor;
      }
    }
  }

 .contacts__form{
   @include flex(space-between, center, row);
   flex-wrap: wrap;
    background-color:$bgcolor;
    width:100%;
    max-width: 549px;
    padding: 99px 167px 0;
    margin: -109px 0 0 22px;
    @include placeholder($textcolor);


    input[type=text]{
      font-family: FuturaBookC;
      font-size: 18px;
      line-height: 24px;
      border:none;
      border-bottom: 1px solid rgba(255,255,255,0.5);
      font-weight: 400;
      padding: 3px 2px 4px 0;
      margin: 0 0 0 10px;
      outline: none;
      color: $textcolor;
      background: none;
    }

    input[name=name], input[name=phone]{
      max-width:233px;
      width:100%;
    }

    input[name=question]{
      width:520px;
    }
  }

  div{
    font-family: FuturaBookC;

    &:before, &:after{
      content: '';
      font-size: 15px;
      line-height: 20px;
      color:rgba(255,255,255,0.5);
      position: relative;
    }
  }

  .contacts__form__input--name::before{
    content:'Name';
    top:4px;
    left:10px;
  }

  .contacts__form__input--phone::before{
    content:'Phone';
    top:4px;
    left:8px;
  }

  .contacts__form__input--question::before{
    content:'Question';
    top:4px;
    left:10px;
  }

  .contacts__form__input--agree::after{
    content:'I agree to the processing of personal data';
    top: 2px;
    left: 8px;
    letter-spacing: 0.3px;
  }

  .contacts__form__input--name{
    margin: 0 0px 0 3px;
  }

  .contacts__form__input--phone{
    margin: 0 32px 0px 0;
  }

  .contacts__form__input--question{
    margin: 42px 0 33px 4px;
  }

  div.contacts__form__input--agree{
    display: flex;
    cursor: pointer;
    position: relative;
    width: 70%;
    margin: 11px 0 48px 127px;

    input {
      height: 18px;
      width: 18px;
      -webkit-appearance: none;
      -moz-appearance: none;
      -o-appearance: none;
      appearance: none;
      border: 1px solid $textcolor2;
      border-radius: 2px;
      outline: none;
      transition-duration: 0.3s;
      background-color: $textcolor;
      cursor: pointer;
      position: relative;

      &:checked{
        border: 1px solid $textcolor2;
        background-color: $textcolor2;
      }

      &:checked + span::before{
        content: url('data:image/svg+xml;charset=UTF-8, <svg width="10" height="8" viewBox="0 0 10 8" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M9.03657 0.687256L3.32504 6.39879L0.728882 3.80264" stroke="white" stroke-width="1.5"/></svg>');
        display: block;
        text-align: center;
        position: absolute;
        left: 9px;
        top: 3px;
      }
      

      &:active{
        border: 2px solid #34495E;
      }
    }

  }

  .contacts__form__input--submit{
    width: 42%;
    margin:0 auto 35px auto;

    input[type=submit]{
      background: $textcolor2;
      color: $textcolor;
      font-family: "FuturaDemiC";
      font-size: 15px;
      font-weight: 400;
      line-height: 26.37px;
      text-transform: uppercase;
      border: 1px solid $textcolor2;
      width:200px;
      margin: 0 16px;
      padding: 11px 0;
      cursor: pointer;
      outline: none;

      &:hover{
        color: $textcolor2;
        background: none;
        border: 1px solid $textcolor2;
      } 
    }
  }

  @media screen and (max-width: $break-laptop) {
    ul{
      width:10%;
    }

    div.map {
      @include flex(center, center, row);
      margin: 0 auto 58px auto;

      img{
        width:80%;
      }
    }
  }

  @media screen and (max-width: $break-smart) {
    background-image: none;
    background-color: $bgcolor;

    h2{
      margin: -10px 0 49px 0px;
      order: 1; 
    }

    .contacts__form{
      margin: -103px 0 0 10px;
      padding: 99px 7px 0;
      max-width: 379px;
      order: 2;

      & > div{
        width: 100%;

        input[type=text]{
          max-width: 338px;
          width: 100%;
        }
      }
    }

    .contacts__form__input--name{
      margin: 0 0px 57px 3px;
      width: 100%;

    }
  
    .contacts__form__input--phone{
      margin: 0 32px 13px 0;
      width: 100%;
    }
  
    .contacts__form__input--question{
      margin: 42px 0 33px 4px;
      width: 100%;
    }
  
    div.contacts__form__input--agree{
      margin: -14px 0 64px 9px;
    }
  
    .contacts__form__input--submit{
      input[type=submit]{
        width: 100%;
        max-width: 335px;
      }
    }

    p{
      order:4;
      font-size: 18px;
      line-height: 25px;
    }

    ul{
      order:5;
      width: 26%;
      max-width: 100px;

      a{
        width:36px;
        height:36px;
      }

    }

    div.map{
      order:3;
      margin: 12px 0 74px 0;
      
      img{
        width:90%;

      }
    }
    
  }

  
}
